import { useEffect, useState } from "react"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Drawer, DrawerProps } from "@rp/react/web/mui/core"

type Props = DrawerProps

const MobileDrawer = ({ children, open, ...props }: Props) => {
  const [drawerOpen, setDrawerOpen] = useState<boolean | undefined>(false)
  const { isMobile } = useDeviceInfo()
  useEffect(() => {
    setTimeout(() => {
      setDrawerOpen(open)
    })
  }, [open])

  useEffect(() => {
    const body = document.body
    const hasScrollbar = !isMobile && window.innerHeight < document.body.scrollHeight
    if (open) {
      hasScrollbar
        ? body.classList.add("freezeScroll", "withScrollBar")
        : body.classList.add("freezeScroll")
    }
    return () => {
      body.classList.remove("freezeScroll", "withScrollBar")
    }
  }, [open, isMobile])

  return (
    <Drawer {...props} open={drawerOpen} anchor="bottom">
      {children}
    </Drawer>
  )
}

export { MobileDrawer }
